:root {
  --bg-black: #090909;
  --white: #f8f8ff;
  --orange: #FB5607;

}

html, body {
  width: 100% !important;
  height: 100%;
  box-sizing: border-box;
  color: #eaecef;
  background-color: var(--bg-black);
}
body {
overflow-x: hidden;
  max-width: 100%;
}
body::-webkit-scrollbar {
  width: 1em;
}
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border:1px solid #373737;
}
body::-webkit-scrollbar-thumb {
 background-color: #FB5607;
 max-height: 50%;
 height: 9.81%;
}
.links {
  display: flex;
  justify-content: flex-end;
}
.links a {
  margin-top: 3px;
  text-decoration: none;
  color: var(--bg-black);
  font-size: 20px;
  font-weight: 700;
  padding: 3px 20px;
  transform: translateY(5px);
  background-color: cadetblue;
  margin-right: 15px;
  margin-bottom: 20px;
  border: 2px solid #555;
  letter-spacing: 1.5px;
  text-align: center;
}
.links a:after {
  content: "";
  background: darkred;
  display: block;
  position: absolute;
  padding-top: 150%;
  padding-left: 109%;
  margin-left: -20px!important;
  margin-top: -90%;
  opacity: 0;
  transition: all 0.5s
}

.links a:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s
}

/*.home-div {
  background-image: url('./images/avatar.png');
}*/ /*-----------------------------------------------------*/
header {
 
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--bg-black);
  height: 60px;
  width: 100%;
  overflow: hidden;
}
header p {
  margin-right: 80px;
  color: var(--white);
  font-size: 1.2em;
  font-family: sans-serif;
  animation: animate 5s linear infinite;
}
header p::after {
  content:'________';
  margin-left: -80px;
  color: #353535;
  animation: animate 5s linear infinite;
}
header span {
  color: #FB5607;
}
header svg {
  font-size: 40px;
  margin-left: 10px;
  color: var(--white);
}

.mainDiv {
  max-width: 60vw;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  margin: 20px;
  background-color: #101010;
  color: lightgray;
  padding: 1.5rem;
  box-shadow:  0px 2px 5px rgba(98,34,3,0.5);
  border:1px solid white;
  box-shadow: 0 0 0 1px #FB5607,
              0 0 0 2px #6930c3;
}
.mainDiv img {
  width: 85%;
 /* height: 50%;
  padding: 10px 0 10px 0;
  object-fit: contain;*/
  margin: 0 auto;
}
.main {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: #c9c9c9;
}
.main-title {
  margin-left: 10px;
  padding-top: 20px;
}
.sidenav {
  width: 11%;
  background-color: #141d26;
  position: fixed;
  height: 51vh;
  box-shadow: 1px 1px 1px 1px var(--white);
  outline: none;
  padding: 0 15px;
  left: -110%;
  border: none;
  transition: all 0.4s;
  z-index: 5;
}
.sidenav.active {
  left: 0;
}

/*.name {
    width: 70px;
    font-size: 20px;
    align-items: center;
    word-wrap: break-word;
  
}*/
.sidenav svg {
 display: flex;
 justify-content: space-between;
 padding: 10px;
 align-items: center;
}
.navbar-svg {
  display: flex;
  margin-right: 20px;
}
.sidenav svg:hover {
  background-color: lightgray;
  border-radius: 15px;
  transition: all 0.4s;
 }
 svg {
   margin-left: 13px;
 }

.sidenav ul {
  padding: 0;
  margin-left: 20px;
}
.sidenav ul li {
  list-style-type: none;
  margin: 10px;
}
.sidenav ul li a {
  text-decoration: none;
  display: block;
  border-radius: 6px;
  padding: 10px 15px; 
  color: var(--orange);
  letter-spacing: 1.5px;
}
ul li a:hover,
ul li a:active {
  background-color: #172b4d;
}

.giHam {
  cursor: pointer;
  font-size: 40px;
}
.giHam:active {
  transform: rotate(320deg);
}
.load-more {
  padding: 8px 35px;
  position: absolute;
  left: 54%;
  font-size: 1.3rem;
  transform: translate(-55%);
  width: 70%;
  margin: 20px 0 20px 0 !important;
  border: none;
  border-radius: 3px;
  background-color:  var(--orange);
  color: var(--white); 
}
.load-more:hover {
  background-color:  var(--white);
  color: var(--orange);
}
.spinner {
  position: absolute;
  left: 48%;
  top: 45%;
  transform: translate(-48%,-45%);
}
.input-search {
  max-width: 45%;
  width: 40%;
  height: 40px;
  font-size: 20px;
  padding-left: 10px;
  position: absolute;
  top: 18.3%;
  border-radius: 5px;
  margin-bottom: 30px !important;
  margin-left: 100px;
  background-color: var(--orange);
  color: var(--white); 
  z-index: 1;
}
::placeholder {
  color: var(--white);
}
@media only screen and (min-width: 100px) and ( max-width:300px) {
  body {
    margin: 0 auto;
  }
  header {
    padding:20px;
    background-color: var(--bg-black);
    height: 30px;
    width: 100%;
  }
  .main {
    margin: 0 auto;
  }
  .sidenav {
    width: 100%;
    background-color: var(--bg-black);
    position: absolute;
    height: 30vh;
    outline: none;
    border: none;
    opacity: 1;
    padding: 0 30px;
    left: -120%;
    transition: all 0.4s;
    border-top-right-radius: 48.5%;
  }
  header svg {
    font-size: 24px;
    color: var(--white);
  }
  ul li a {
    color: #eaecef;
    text-decoration: none;
    font-size: 18px;
    display: block;
    padding: 6px 8px;
    border:none;
  }

  .mainDiv {
    width:80%;
    margin: 0 auto;
    font-size: 15px;
    
  }
  .mainDiv img {
    width: 100%;
  }
  .input-search {
    margin-top: 30px;
  }
}
