/*Portfolio css */
.main-portfolio {
  display: block;
  margin-left:40px;
}
.main-portfolio p {
  padding: 0 !important;
}
.main-portfolio img {
  width: 160px;
  height: 185px;
}
.main-portfolio .arrow {
  font-size: 45px;
  margin-top: 28px;
  position: absolute;
  left: 50%;
  transform: translate(-50%) rotate(90deg);
  animation: arrow 1.5s infinite ease-in-out;
  transition: 0.3s all;
}
@keyframes arrow {
  0% {
    margin-left: 13px;
  }
  50% {
    margin-top: 10px;
  }
  75% {
    margin-bottom: 10px;
  }
  100% {
    margin-right: 10px;
  }
}
.portfolio {
  background-color:#FECF0C;
  padding:10px;
  width: 88%;
  margin: a auto;
  color: var(--bg-black);
  border: 3px solid #555;
  line-height: 1.5;
  font-weight: 800;
  word-break: break-word;
  animation: animate 10s linear infinite;
}
@keyframes animate
{
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

.portfolio h2,
.portfolio h4 {
  padding: 0;
  margin: 0;
}
.main-portfolio h6 {
  background: #151515;
  padding: 72.5px 30px 66px 25px;
  margin-top: -1px;
  font-size: 18px;
}
.avatar {
  display: flex;
  color: #eaecef;
  padding: 0;
}
.avatar h5 {
  font-size: 24px;
  padding-left: 17px;
  word-break: break-word;
}
.welcome-page {
background-color: #000000;
opacity: 0.5;
width: 95%;
height: 90vh;
margin: 0 auto;
color: var(--white) i!important;
margin-left: -1px;
line-height: 1.5;
padding: 20px;
background-image: radial-gradient(#206953 1px, #000000 1px);
background-size: 20px 20px;
word-wrap: break-word;
}
.copyright {
  margin-top: 140px;
  text-align: center;
}
.projects {
  display: flex;
  flex-direction: column;
  margin: 18px;
}
.projects.item {
  border-top: 1px solid gray;
  border-left: 1px solid gray;
  padding: 20px;
  align-items: center;
  font-weight: 800;
  letter-spacing: 1.6px;
  margin-left: -15px;
  -webkit-box-shadow: 5px 5px 0px 0px #289FED, 10px 10px 0px 0px #5FB8FF, 15px 15px 0px 0px #A1D8FF, 20px 20px 0px 0px #CAE6FF, 25px 25px 0px 0px #E1EEFF, 1px 1px 10px -2px rgba(24,24,23,0); 
  box-shadow: 5px 5px 0px 0px #289FED, 10px 10px 0px 0px #5FB8FF, 15px 15px 0px 0px #A1D8FF, 20px 20px 0px 0px #CAE6FF, 25px 25px 0px 0px #E1EEFF, 1px 1px 10px -2px rgba(24,24,23,0);
}
.projects.item:hover {
  background-color: #1c9bf0;
  color: var(--bg-black);
}
.demo {
  font-size: 18px;
}
.projects.item a {
  color: #fecf0c;
  letter-spacing: 1.5px;
}